.leaflet-container .marker-cluster {
  background-color: rgba(255, 27, 49, 0.25);
}
.leaflet-container .marker-cluster div {
  background-color: #ff1b31;
  color: white;
}
.leaflet-container .marker-cluster-large {
  background-color: rgba(255, 27, 49, 0.75);
}
.leaflet-container .marker-cluster-large div {
  font-weight: bold;
}
.leaflet-container .marker-cluster-small {
  background-color: rgba(255, 27, 49, 0.15);
}
.leaflet-container .marker-cluster-medium {
  background-color: rgba(255, 27, 49, 0.5);
}
.leaflet-container .leaflet-popup-tip {
  background: #D7D7D7;
  box-shadow: none;
}
.leaflet-container .leaflet-popup-content-wrapper {
  box-shadow: none;
  border-radius: 4px;
  background: #D7D7D7;
}
.leaflet-container .leaflet-popup-content {
  margin: 8px 12px;
}
.leaflet-container .leaflet-popup-content .title {
  font-size: 15px;
  font-weight: bold;
}
.leaflet-container .leaflet-popup-content .ip {
  margin-top: 0.5rem;
  font-family: Consolas, "Andale Mono WT", "Andale Mono", "Lucida Console", "Lucida Sans Typewriter", "DejaVu Sans Mono", "Bitstream Vera Sans Mono", "Liberation Mono", "Nimbus Mono L", Monaco, "Courier New", Courier, monospace;
}



